@import '../../scss/variables';

.header {
	border-bottom: 1px solid $gray-line;
	padding: 40px 0;
	@media (max-width: 960px) {
		padding-bottom: 0px;
		&__logo div p {
			font-size: 20px;
		}
	}

	.container {
		display: flex;
		align-items: center;
		justify-content: space-between;
		@media (max-width: 960px) {
			flex-direction: column;
			gap: 20px;
		}
	}

	&__logo {
		display: flex;
		@media (max-width: 960px) {
			flex-direction: column;
			align-items: center;
			gap: 20px;
			div {
				text-align: center;
			}
			img {
				width: 70px;
			}
		}

		img {
			margin-right: 15px;
		}

		h1 {
			color: #181818;
			font-size: 24px;
			letter-spacing: 1%;
			text-transform: uppercase;
			font-weight: 800;
		}

		p {
			color: #7b7b7b;
		}
	}
}
