@import '../../scss/variables';

.inputBlock {
	padding: 10px;
	padding-left: 40px;
	border: 1px solid #ccc;
	border-radius: 10px;
	width: 200px;
	font-size: 18px;
	transition: 0.4s ease-in-out;
	&:focus {
		width: 400px;
	}
}

.searchImg {
	position: absolute;
	top: 43px / 2 - 12px;
	left: 12px;
	z-index: 2;
	width: 24px;
	height: 24px;
	opacity: 0.5;
}

.valueDeleteImg {
  position: absolute;
  cursor: pointer;
  opacity: 5px;
  top: 43px / 2 - 12px;
  right: 5px;
  opacity: .5;
  transition: .3s ease-in-out;
  &:hover {
    opacity: 1;
    transform: rotate(180deg);
  }
}

.searchBlock {
	left: -70px;
	position: relative;
	@media (max-width: 1280px) {
		left: 0;
		.inputBlock {
			transition: 0.2s ease-in-out;
			&:focus {
				width: 260px;
			}
		}
	}
	@media (max-width: 960px) {
		left: 0;
		.inputBlock {
			width: 350px;
			&:focus {
				width: 450px;
			}
		}
	}
	@media (max-width: 580px) {
		left: 0;
		.inputBlock {
			width: 100%;
			&:focus {
				width: 100%;
			}
		}
	}
}

